$component: --mt-quadro-01;

:host {
  #{$component}-span-color: var(--mt-color-quadros-fonte-01);
  #{$component}-span-font-family: 'Noto Sans', sans-serif;
  #{$component}-span-font-weight: 900;
  #{$component}-span-text-transform: uppercase;
  #{$component}-span-font-size: 16px;
  #{$component}-span-padding: 6px;
  #{$component}-span-background-color: var(--mt-color-quadros-bg-01);

  #{$component}-container-border: 2px solid var(--mt-color-quadros-fonte-02);

  #{$component}-text-color: var(--mt-color-quadros-fonte-02);
  #{$component}-text-font-family: 'Noto Sans', sans-serif;
  #{$component}-text-font-weight: 500;
  #{$component}-text-text-transform: none;
  #{$component}-text-font-size: 16px;
  #{$component}-text-padding: 6px;

  display: block;
}

.mt-quadro-01 {
  &__container {
    border-left: var(#{$component}-container-border);
  }

  &__span {
    color: var(#{$component}-span-color);
    font-family: var(#{$component}-span-font-family);
    font-weight: var(#{$component}-span-font-weight);
    text-transform: var(#{$component}-span-text-transform);
    font-size: var(#{$component}-span-font-size);
    line-height: 1;
    margin: 0;
    padding: var(#{$component}-span-padding);
    background-color: var(#{$component}-span-background-color);
    display: inline-block;
    border-left: var(#{$component}-container-border);
  }

  &__text {
    color: var(#{$component}-text-color);
    font-family: var(#{$component}-text-font-family);
    font-weight: var(#{$component}-text-font-weight);
    text-transform: var(#{$component}-text-text-transform);
    font-size: var(#{$component}-text-font-size);
    line-height: 1;
    margin: 0;
    padding: var(#{$component}-text-padding);
  }
}
